html {
    --sporeText: #ad9c9cad;
}
 

.spore.showing-spore-player {
    .caurosel {
        max-width: 240px !important;
    }
}

.spore.showing-spore-player, .spore.showing-spores {
    .caurosel.player-chosen {
        position: absolute;
        left: 0px;
        top: 0px;
        height: 100%;
        width: 180px;
        margin: unset !important;
        margin-top: unset !important;

        .spore-player-option {
            margin: 0px !important;
        }
        .spore-player-option.chosen {
            display: flex;
            flex-direction: column;
            padding-left: 15px;
            padding-right: 15px;
            margin-left: 0px;
            margin-right: 0px;
            padding-top: 15px;
            margin-top: unset;
            height: 100%;
            img {
                display: none;
            }
        }
    }
}

.spore {
    .caurosel.player-chosen {
        margin-top: 5%;
        margin-bottom: unset !important;
        height: 40%;
        min-height: 200px;

        img {
        }
    }
    .caurosel {
        position: relative;

        .spore-player-option.chosen {
            animation-name: move-to-right;
            animation-duration: 1s;
            background-color: unset;
            margin-right: 25px;
            margin-bottom: unset;
            img {
                width: 160px;
                animation-name: chosen-image;
                animation-duration: 1s;
            }
        }

        .spore-player-option.unchosen {
            animation-name: spore-player-unchosen;
            animation-duration: .8s;
            width: 0px;
            opacity: 0;
            margin-right: 0px;
        }

        .spore-player-option {
            position: relative;
            overflow: hidden;

            .song-title {
                margin-left: 1px;
                margin-top: 15px;

                .song-name {
                }

                .song-artist {
                    font-size: 10pt;
                    color: var(--sporeText);
                }
            }

            .mouse-radial {
                animation-name: mouse-light;
                animation-duration: 1s;
                opacity: 0.3;
            }
        }
        .mouse-radial {
            z-index: 3;
            pointer-events: none;
            width: 1000px;
            height: 1000px;
            opacity: 0.15;
            // background-image: radial-gradient(#ffffff17, transparent);
            //background: radial-gradient(800px circle at var(--cursor-x) var(--cursor-y),rgba(255,255,255,0.3),transparent 40%);
            position: absolute;
            background: radial-gradient(800px circle,rgba(255,255,255,0.3),transparent 40%);
        }

        margin: auto;
        left: 0px;
        right: 0px;
        top: 0px;
        bottom: 0px;

        display: flex;
        z-index: 2;

        .spore-player-option {
            margin-right: 15px;
            img {
                opacity: 0.92;
                backdrop-filter: blur(4px);
                width: 260px;
                border-radius: 12px;
            }

            .sp-fields:hover {
                animation-name: hover-underline;
                animation-duration: 1s;
                border-bottom-color: white;
            }

            .sp-fields {
                display: flex;
                margin-top: 10px;
                border-bottom: 1px solid transparent;
                cursor: pointer;

                .sp-name {
                    margin-left: auto;
                    white-space: nowrap;
                }

                .sp-rarity {
                    white-space: nowrap;
                    margin-left: 15px;
                    color: var(--sporeText);
                    margin-right: auto;
                }
            }
        }
    }
}

@keyframes mouse-light {
    from {
        //transform: scale(0);
        opacity: 0;
    }

    to {
        opacity: .3;
    }
}


@keyframes spore-player-unchosen {
    from {
        //transform: scale(0);
        width: 260px;
        opacity: 1;
    }

    to {
        width: 0px;
        opacity: 0;
    }
}

@keyframes chosen-image {
    from {
        //transform: scale(0);
        width: 260px;
    }

    to {
        width: 160px;
    }
}


@keyframes hover-underline{
    from {
        border-bottom-color: transparent;
    }

    to {
        border-bottom-color: white;
    }
}







@media screen and (max-width: 600px) {
    .spore.showing-spore-player {
        .caurosel.player-chosen {
            height: 30px;
        }

        .spore-player-option {
            width: 100%;
            margin-top: 15px !important;
            flex-direction: row-reverse !important;

            .sp-fields, .song-title {
                display: flex;
                flex-direction: column;

                .sp-rarity {
                    display: none;
                }
            }

            .song-title {
                margin-top: unset;
                margin-right: auto;
                margin-left: 10px;

                .song-name {
                    margin-right: 10px;
                    //margin-left: auto;
                }
            }
        }
    }

    .spore {
        .caurosel.player-chosen {
            height: 35%;
            min-height: unset;
        }
    }
    .spore, .spore.showing-spore-player {
        .caurosel.player-chosen {
            width: 100%;
            margin-top: 15px !important;
        }
 
        .spore-player-option.chosen {
            padding-left: 10px !important;
        }

        .spore-player-option.unchosen {
            display: none !important;
        }

   }
}
