@font-face {
  font-family: 'stem';
  font-style: normal;
  font-weight: 300;
  src: url(/stem.woff) format('woff');
}

body {
    color: white;
}

.pushable:active.pushable:hover {
    transform: scale(1) !important;
    animation-name: click-btn !important;
    animation-duration: 0.2s;
}

.pushable:hover {
    transform: scale(1.1);
    animation-name: hover-btn !important;
    animation-duration: 0.2s;
    cursor: pointer;
}
 
@keyframes hover-btn {
    from {
        transform: scale(1);
    }

    to {
        transform: scale(1.1);
    }
}

@keyframes click-btn {
    from {
        transform: scale(1.1);
    }

    to {
        transform: scale(1);
    }
}

