html,
body,
#root {
  margin: 0;
  height: 100%;
  background-color: black;
  overflow: hidden;
}

.canvas-container {
  position: absolute;
  bottom: 25px;
  right: 40px;
  height: 200px;
  width: 266px;

iframe {
  border-radius: 30px;
  border-color: transparent;
  user-select: none;
}

}

@media screen and (max-width: 600px) {
    .canvas-container {
        right: unset;
        left: -65px;
        bottom: -50px;
        //transform: scale(0.6);
    }
}

