.play-btn {
     width: 20px;
     height: 20px;
     border-style: solid;
     border-width: 10px 0px 10px 16px;
     border-color: transparent transparent transparent white; 
}

.play-btn.playing {
    width: 20px;
    height: 20px;
    border-color: white;
    border-style: double;
    border-width: 0px 0px 0px 20px;
    margin-left: -3px;
}

.play-btn-wrapper {
    border-radius: 15px;
    background-color: #23222224;
    margin-right: 15px;
    width: 65px;
    height: 70px;
    padding: 22px;
    padding-top: 20px;
    border: 4px solid #1b1b1b;
    margin-top: 20px;
}
