.song-credits {
    font-size: 8pt;
    margin-top: 20px;
    border-top: 1px solid #ffffff3b;
    padding-top: 15px;

    .credits-btn.show {
        border-bottom: 1px solid white;
    }

    .credits-btn {
        font-size: 10pt;
        width: 50px;
        margin-bottom: 15px;
    }

    .credit-part {

        display: flex;
        flex-direction: column;
        margin-bottom: 15px;

        .credit-names {
            display: flex;

            .credit-name {
                margin-right: 4px;
                color: var(--sporeText);
            }
        }
    }
}
