.spore {
    .credits, .player-helper {
       .x-button {
           position: absolute;
           z-index: 7;
           position: absolute;
           right: 65px;
           color: #716e73;
           top: 15px;
           font-size: 46pt;
       }
    }
    .credits {
        position: absolute;
        z-index: 6;
        width: 100%;
        height: 100%;
        top: 0px;
        left: 0px;
       background-color: #00000091;
       display: flex;
       backdrop-filter: blur(7px);

       .x-button {
           position: absolute;
           z-index: 7;
           position: absolute;
           right: 65px;
           color: #716e73;
           top: 15px;
           font-size: 46pt;
       }

        .credits-text {
            width: 400px;
            margin: auto;
            left: 0px;
            top: 0px;
            right: 0px;
            bottom: 0px;
        }

    }
}
