.spores-playlist {
    margin-top: 30px;
    margin-left: -15px;
    margin-right: -15px;
    .song-item:hover {
        background-color: #ffffff2f;
    }

    .song-item.current{  
        color: black;
        background-color: white;
    }
    .artist-name {
        color: #aab7ba;
        font-size: 10pt;
        margin-bottom: 5px;
    }
    .song-item {
        font-size: 9pt;
        display: flex;
        padding: 2px;
        padding-left: 15px;

        .pulse-loader {
            margin-top: 3px;
            margin-left: 10px;
        }
    }
}

@keyframes fade-in-menu {
    from {
        height: 0px;
        opacity: 0;
    }

    to {
        height: 100px;
        opacity: 1;
    }
}
