.spore {
    opacity: 1;
    ::-webkit-scrollbar-thumb {
        background: linear-gradient(to right, #333336d6, #494343bf), url(https://grainy-gradients.vercel.app/noise.svg) !important;
    }
    display: flex;
    flex-direction: column;
    height: 100%;
    font-family: stem !important;
    overflow: hidden;
    animation-name: fade-in-page;
    animation-duration: 3s;

    .view-on-opensea.stem-player-button {
        font-size: 12pt !important;
        margin-top: 30px !important;
        width: 140px !important;
        span {
            font-size: 16pt;
            overflow: hidden;
            line-height: 10px;
        }
    }

    .explainer-button:hover {
        color: white;
        animation: hover-explainer .72s ease;
    }

    .explainer-button {
        animation: explainer-blink .72s ease infinite;
        transform-origin: 50% 50%;
        animation-direction: alternate;
        position: absolute;
        bottom: 35px;
        left: 35px;
        color: #716e73;
        font-size: 46pt;
        cursor: pointer;
        z-index: 3;
    }

    .simple-player.hide {
        .audio-player {
            display: none;
        }
    }
    
    .list-and-player {
        display: flex;
        margin-right: auto;
        margin-left: auto;
        margin-top: 7%;
        margin-bottom: auto;


        .progress-bar {
            position: relative;

            margin-left: 0px;
        }


        .simple-player {
            margin-top: 15px;
            animation-name: open-player !important;
            animation-duration: 1s;
            width: 300px;

            .play-time-and-slider {
                margin-left: auto;
            }

            .audio-player {
                margin-left: unset;
            }

            .mint-spore:hover {
                .spore-tooltip {
                    display: block;
                }
            }
            .mint-spore {
                background: linear-gradient(to right, #757575d6, #9b9b9bbf), url(https://grainy-gradients.vercel.app/noise.svg);
                cursor: not-allowed;
                position: relative;
                font-size: 12pt;
                opacity: 0.5;
                overflow: visible;
                .spore-tooltip {
                    display: none;
                    position: absolute;
                    top: 70px;
                    left: 0px;
                    padding: 5px;
                    background-color: white;
                    color: black;
                }
            }
        }

        .spores-list {
            margin-right: 20px;
        }
    }

    .spores-list {
        animation-name: open-list!important;
        animation-duration: 1.6s;
        z-index: 1;
        min-height: 400px;
        transform: scale(1);
        max-height: 65%;
        overflow: scroll;
        padding-left: 15px;
        padding-right: 15px;
        backdrop-filter: blur(4px);
        //background-color: #ffffff0f;
        padding: 15px;
        border-radius: 15px;
        background-image: radial-gradient(#39313133, #76728238);

        .spore-item:hover, .spore-item.selected {
            color: black;
            background-color: white;
        }

        .spore-item:active {
            //color: white;
            //background-color: transparent;
            //animation-name: click-spore !important;
            //animation-duration: 0.2s;
        }

        .spore-item {
            border: 1px solid transparent;
            cursor: pointer;
            padding: 5px;
            width: 390px;
            display: flex;

            .spore-id {
                width: 80px;
            }

            svg.heatmap {
                margin-top: 7px;
                margin-right: 12px;
            }

            .hyphen {
                height: 3px;
                //background: linear-gradient(to right, #333336d6, #494343bf), url(https://grainy-gradients.vercel.app/noise.svg) !important;
                background-image: linear-gradient(319deg, #ffbd77 0%, #f0f4a4 37%, #acfcd9 100%);
                margin-right: 13px;
                width: 15px;
                margin-top: 11px;
            }

            .spore-date {
                margin-left: auto;
            }
        }
    }

    .no-spores {
        text-align: center;
    }
   
    canvas {
        /*
        position: absolute;
        left: 0px;
        top: 0px;
        width: 100% !important;
        height: 100% !important;
        z-index: 0 !important;
        */
    }
    .z-object {
        z-index: 0 !important;
        border: none;
        .inlets, .outlets {
            display: none;
        }
    }

    .stem-player-button {
        z-index: 1;
        margin-top: 50px;
        width: 200px;
        padding: 10px;
        margin-left: auto;
        margin-right: auto;
        font-size: 16pt;
        border-radius: 40px;
        text-align: center;
        background-color: #3c3a3a;
        background: linear-gradient(to right, #333336d6, #494343bf), url(https://grainy-gradients.vercel.app/noise.svg);
    }

    .stem-player-button.spore-button {
        background-color: transparent;
        background-image: unset;
        position: absolute;
        bottom: 35px;
        right: 35px;
        padding-left: 20px;
        padding-right: 20px;
        width: unset;
        img.spore-icon.one-spore {
            margin-left: 10px;
            width: 20px;
        }
        img.spore-icon {
            margin-top: -4px;
            width: 30px;
        }
    }

    .progress-bar {
        margin-left: -60px;
        z-index: 5;
        background-color: #3c3a3a !important;
        top: 0%;
        position: relative;
        width: 387px;
        padding: 2px;

        .loading-text {
            font-size: 14pt;
            top: 5px;
            left: 10px;
            text-align: left;
            padding-left: 35px;

        }

        height: 45px;

        .inner-progress-bar {
            position: relative;
            width: 370px;
            .progress {
                top: -6px;
                position: absolute;
                height: 40px;
                background: linear-gradient(to right, #333336, #8e4c4c78),url(https://grainy-gradients.vercel.app/noise.svg)
            }
        }
    }

    .simple-player {
        z-index: 1;
        width: 260px;
        height: 200px;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: auto;
        display: flex;
        flex-direction: column;

        .audio-player {
            display: flex;
            margin-left: -180px;
            
            .song-title-and-author {
                display: flex;

                .song-creator {
                    margin-left: auto;
                }
            }
            .play-time-and-slider {
                margin-left: auto;

                .play-time {
                    display: flex;

                    .creator {
                        margin-left: auto;
                    }
                }
            }

            .play-slider {
                margin-top: 10px;
                width: 250px;
                height: 15px;
                background-color: #333336;
                position: relative;
                margin-bottom: 20px;

                .mouse-seeker {
                    z-index: 100;
                    position: absolute;
                    opacity: 0;
                    width: 2px;
                    top: 0px;
                    height: 15px;
                    background-color: white;
                    animation-name: mouse-seeker;
                    animation-duration: 0.4s;
                }

                svg.heatmap {
                    position: absolute;
                    left: 0px;
                    z-index: 3;
                    top: 18px;
                    margin-top: unset;
                }

                .play-slider-inner {
                    position: absolute;
                    left: 0px;
                    top: 0px;
                    z-index: 4;
                    height: 15px;
                    background: linear-gradient(to right, #cacaf0, #8e4c4c78), url(https://grainy-gradients.vercel.app/noise.svg);
                }
            }
        }

        .playpause {
            width: 40px;
            padding-top: 25px;

            label {
                width: 40px;
            }
        }
        .playpause label {
            height: 28px;
            border-width: 0px 0 0px 22px;
            margin-right: 0px;
        }

        .playpause input[type="checkbox"]:checked + label {
            border-style: solid;
            height: 10px;
            border-width: 15px 0 15px 18px;
        }

   }

    iframe {
        animation-name: open-list !important;
        animation-duration: 2s;
        border-radius: 30px;
        z-index: 1;
        margin-top: auto;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: auto;
        box-shadow: rgb(87 84 84 / 40%) 0px 12px 28px 0px, rgb(0 0 0 / 35%) 0px 2px 4px 0px, rgb(0 0 0 / 16%) 0px 0px 0px 1px inset;
    }
}

.spore.showing-spore-player {
    .explainer-button {
        font-size: 46pt;
    }
}

@media screen and (max-width: 600px) {
    .bad-scroll {
        .spore {
            overflow: hidden;
        }
    }
    .spore {

        .simple-player {
            
            .audio-player {
                margin-left: -30px;
            }
        }
 
        overflow-y: scroll;

        .nft-mode.v3 {
            .progress-bar {
                bottom: 0px !important;
            }
            
        }
        .progress-bar {
            z-index: 5;
            background-color: #3c3a3a !important;
            bottom: -450px !important;
        }
    }
 
    .explainer-button {
        display: none;
    }
    .spore.showing-spore-player {
        .spore-player-option.chosen {
            padding-top: 3px !important;
            height: 50px !important;
            min-height: 50px !important;
            pointer-events: none;

            .sp-fields {
                .sp-name {
                    margin-left: unset;
                }
            }
            img {
                display:  none;
            }
        }

        .caurosel {
            // display: none;
            // height: 10px !important;

            .spore-player-option {
                padding-top: 3px !important;
                // height: 20px !important;
            }
        }
    }

    
    .spore.showing-spores {
        .caurosel {
            //height: 20px !important;
            pointer-events: none !important;

            .spore-player-option {
                // padding-top: 35px !important;

                .sp-fields {
                    margin-top: 0px;
                }
            }
        }

        .spore-player-option.chosen {
            width: 250px !important;
            height: 30px !important;
        }

        .list-and-player {
            margin-top: 70px;
            flex-direction: column;

            .mint-spore.stem-player-button {
                display: none;
            }

            .spores-list {
                width: 98%;
                max-height: 300px;
                min-height: unset;
                background-color: transparent;
                background-image: none;

                .spore-item {
                    width: 99%;
                   
                    .spore-date {
                        font-size: 8pt;
                        line-height: 25px;
                    }
                }
            }

            .simple-player {
                margin-left: 0px;
                margin-bottom: 100px;

                .audio-player {
                    margin-top: 15px;
                    margin-left: 25px;
                }
            }
        }
    }

    .spore {
        .caurosel.player-chosen {
            background-color: unset !important;

            img {
                display: none;
            }

            .spore-player-option {
                margin-top: 0px;
            }
        }

        .caurosel {
            flex-direction: column;
            margin-left: 30px;

            .spore-player-option {
                display: flex;
                margin-left: 0px;
                animation-name: unset !important;
                background-color: #a8a8a829;
                padding: 20px;
                margin-top: 20px;

                .sp-fields {
                    margin-left: 15px;
                    margin-top: auto;
                }
                img {
                    width: 120px;
                }
            }
        }
    }
    .spore {
        .stem-player-button.spore-button {
            position: absolute;
            bottom: 8px;
            right: 20px;
            font-size: 14pt;
        }
    }

}

@keyframes click-spore {
    from {
        background-color: white;
        color: black;
    }

    to {
        color: white;
        background-color: transparent;
    }
}

@keyframes open-player {
    from {
        width: 0px;
        opacity: 0;
    }

    to {
        width: 300px;
        opacity: 1;
    }
}

@keyframes open-list {
    from {
        //transform: scale(0);
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}


@keyframes explainer-blink {
    from {
        transform: scale(1);
    }

    to {
        transform: scale(1.1);
    }
}

@keyframes hover-explainer {
    from {
        color: #716e73;
    }
    to {
        color: white;
    }
}

@keyframes mouse-seeker {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

@keyframes fade-in-page {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}






.rain-video {
    position: absolute;
    z-index: 0;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    object-fit: fill;
}

button:active {
    outline: none !important;
}
