.scrollContainer {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    overflow-y: auto;
    will-change: transform;
    color: var(--color-text-main);
    /* font-family: "Spline Sans", sans-serif; */
    font-family: stem;
  }

  .spButtons {
      display: flex;
      flex-wrap: wrap;
  }

  .spButton {
      padding-left: 10px;
      padding-right: 10px;
      font-size: 14pt;
    border-radius: 20px;
    border-style: none;
    margin-bottom: 10px;
    margin-left: 10px;
    /* font-family: "Spline Sans", sans-serif; */
    cursor: pointer;
    background-color: #000000d4;
    color: rgba(255, 255, 255, 0.8);
  }

  .logo {
    height: 20px;
    margin-left: 15px;
  }

  .zeqLogo {
    height: 35px;
  }

  #x {
    height: 10px;
    margin-bottom: 5px;
    margin-left: 15px;
  }

  .smallText {
    font-size: 14px;
    margin-bottom: 0px;
    color:rgba(255, 255, 255, 0.8);
  }
  
  section {
    display: flex !important;
    flex-direction: column;
    justify-content: center;
  }
  
  section p {
    font-size: 32px;
    line-height: 38px;
  }
  
  .sectionHero {
    justify-content: initial;
  }
  
  .sectionLeft {
    padding: 0 62px;
  }

  .sectionFirst {
      color: #ffffff59;
  }

  .sectionSecond {
      color: #ffffffa8;
  }

  .sectionLast {
      color: white;
  }
  
  .sectionRight {
    text-align: right;
    padding: 0 62px;
    align-items: flex-end;
  }
  
  header {
    margin-top: 50px;
    margin-bottom: 200px;
    position: relative;
  }
  
  header h1 {
    font-weight: 600;
    font-size: 54px;
    margin: 11px 0;
    color: var(--color-title);
    color: #e3d1d8bf;
  }
  
  header strong {
    color: var(--color-highlight);
  }
  
  header sup {
    display: inline-block;
    color: var(--color-cta-text);
    background: var(--color-cta-bg);
    border-radius: 4px;
    font-size: 14px;
    line-height: 151.5%;
    padding: 1px 9px;
    position: relative;
    top: -48px;
    left: -25px;
  }
  
  nav {
    font-size: 32px;
    line-height: 38px;
    width: 150px;
  }
  
  nav a {
    display: block;
    color: var(--var-color-text-main);
  }
  
  a {
    text-decoration: none;
    color: var(--color-highlight);
  }
  
  a:hover {
    text-decoration: underline;
    color: var(--color-highlight);
  }
  
  @media only screen and (max-width: 768px) {
    header {
      margin-top: 38px;
      margin-bottom: 200px;
    }
  
    header h1 {
      font-size: 48px;
    }
  
    header sup {
      font-size: 12px;
      padding: 3px 10px;
      top: -42px;
      left: -24px;
    }
  
    nav,
    section p {
      font-size: 24px;
      line-height: 28px;
    }
  
    .sectionLeft {
      padding: 0 8vw;
    }
  
    .sectionRight {
      padding: 0 8vw;
    }
  }
  
  @media only screen and (max-width: 480px) {
    header {
      margin-top: 30px;
      margin-bottom: 50px;
    }
  
    header h1 {
      font-size: 36px;
    }
  
    header sup {
      font-size: 11px;
      padding: 2px 8px;
      top: -35px;
      left: -18px;
    }
  
    nav,
    section p {
      font-size: 18px;
      line-height: 23px;
    }

    .caroItem {
      height: 300px;
    }

    .playerCarousel {
      margin-top: 75px;
      margin-bottom: 50px;
    }
  }
  
  .selected {
    color: var(--color-highlight) !important;
  }

  .playerCarousel {
    width: 100%;
    height: 400px;
    margin-top: 100px;
    margin-bottom: 75px;
  }

  .noImgSelector {
    user-drag: none;
    -webkit-user-drag: none;
    user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    margin-bottom: 10px;
}

.caroItem {
  text-align: center;
}
  
  
