.spore.doodles {
    background: linear-gradient(180deg,#382653 0,#6681c5);
    background-repeat: no-repeat;
    background-size: cover;
}

.spore.ragnarok {
    background: linear-gradient(180deg, #1c1631 50%, #42256ee0);
    background-repeat: no-repeat;
    background-size: cover;
}

.spore.demo {
    .caurosel {
        background-color: #ffffff0a;
        padding-right: 15px;
    }
}

.spore {
    .password-box {
        margin: auto;
        width: 300px;
        height: 300px;
        display: flex;
        flex-direction: column;

        input {
            background-color: #0000004a;
            outline: none !important;
            border: none;
            border-bottom: 1px solid white;
            color: white;
        }

        .password-enter {
            margin-top: 5px;
            background-color: white;
            color: black;
            padding-left: 5px;
            padding-height: 5px;
        }
    }
}
