.spore {

    .player-helper {

        svg.visual-links-spore {
            position: absolute;
            width: 100%;
            height: 100%;
            z-index: 10000;
            pointer-events: none;
            polygon {
                fill: url(#AnnotationGradient);
            }
        }

        .helper-box {
            position: absolute;
            left: 30px;
            top: 80px;
            width: 300px;
            height: 340px;
            border-radius: 10px;
            padding: 15px;
            border: 2px solid white;

            p.selected {
                background-color: white;
                color: black;
            }
        }

        display: flex;
        backdrop-filter: blur(8px);
        background-color: #000000b8;
        position: absolute;
        left: 0px;
        top: 0px;
        width: 100%;
        height: 100%;
        z-index: 100;

        .mock-player {
            position: relative;
            width: 340px;
            height: 400px;
            margin: auto;
            left: 0px;
            right: 0px;
            top: 0px;
            bottom: 0px;
            border-radius: 30px;
            border: 2px solid white;

            .center-button:hover, .sp-button:hover, .led-button:hover, .sp-slider:hover,
            .center-button.selected, .sp-button.selected, .led-button.seleected, .sp-slider.selected,
            {
                background-color: #a4aed863;
                cursor: pointer;
            }

            .center-button {
                position: absolute;
                width: 45px;
                height: 45px;
                border-radius: 60px;
                border: 2px solid white;
                top: 202px;
                left: 147px
            }

            .sp-button {
                border: 2px solid white;
                border-radius: 18px;
                position: absolute;
                width: 56px;
                height: 56px;
            }

            .sp-button.button2 {
                left: 60px;
                bottom: 20px;
            }

            .sp-button.button3 {
                right: 60px;
                bottom: 20px;
            }

            .led-button.sp-button {
                width: 112px;
                top: 15px;
                left: 115px;
            }

            .sp-slider {
                position: absolute;
                border-radius: 60px;
                border: 2px solid white;
                display: flex;

                span {
                    margin: auto;
                    color: white;
                    font-size: 16pt;
                }
            }

            .slider1 {
                left: 35px;
                top: 127px;
            }

            .slider2 {
                left: 162px;
                top: 262px;
            }
            
            .slider3 {
                left: 72px;
                top: 217px;
            }

            .slider4 {
                left: 207px;
                top: 90px;
            }

            .slider3, .slider4 {
                height: 142px;
                width: 60px;
            }

            .slider1, .slider2 {
                width: 142px;
                height: 60px;
                
            }
        }
    }
}
